
/* eslint no-unused-vars: 0 */
import { ifDefined } from "lit-html/directives/if-defined.js";
import { html, svg, repeat, classMap, styleMap, unsafeHTML, scopeTag } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';

const main = (context, tags, suffix) => {
	const block0 = (context) => { return html`<div class="ui5-sn-root">${ context.showHeader ? block1(context) : undefined }${ context.items.length ? block2(context) : undefined }<div class="ui5-sn-spacer"></div>${ context.fixedItems.length ? block6(context) : undefined }</div> `; };
const block1 = (context) => { return html`<slot name="header"></slot>`; };
const block2 = (context) => { return html`<${scopeTag("ui5-tree", tags, suffix)} id="ui5-sn-items-tree" mode="None" ?_minimal="${ifDefined(context.collapsed)}" _toggle-button-end @ui5-item-click="${ifDefined(context.handleTreeItemClick)}">${ repeat(context._items, (item, index) => item._id || index, (item, index) => block3(item, index, context)) }</${scopeTag("ui5-tree", tags, suffix)}>`; };
const block3 = (item, index, context) => { return html`<${scopeTag("ui5-tree-item", tags, suffix)} icon="${ifDefined(item.item.icon)}" .associatedItem="${ifDefined(item.item)}" text="${ifDefined(item.item.text)}" ?has-children="${item.item.items.length}" ?expanded="${item.item.expanded}" ?selected="${item.selected}">${ !context.collapsed ? block4(item, index, context) : undefined }</${scopeTag("ui5-tree-item", tags, suffix)}>`; };
const block4 = (item, index, context) => { return html`${ repeat(item.item.items, (item, index) => item._id || index, (item, index) => block5(item, index, context)) }`; };
const block5 = (item, index, context) => { return html`<${scopeTag("ui5-tree-item", tags, suffix)} .associatedItem="${ifDefined(item)}" text="${ifDefined(item.text)}" icon="${ifDefined(item.icon)}" ?selected="${item.selected}"></${scopeTag("ui5-tree-item", tags, suffix)}>`; };
const block6 = (context) => { return html`<div><div class="ui5-sn-bottom-content-border"><span></span></div><${scopeTag("ui5-tree", tags, suffix)} id="ui5-sn-fixed-items-tree" mode="None" ?_minimal="${ifDefined(context.collapsed)}" _toggle-button-end @ui5-item-click="${ifDefined(context.handleTreeItemClick)}">${ repeat(context._fixedItems, (item, index) => item._id || index, (item, index) => block7(item, index, context)) }</${scopeTag("ui5-tree", tags, suffix)}></div>`; };
const block7 = (item, index, context) => { return html`<${scopeTag("ui5-tree-item", tags, suffix)} icon="${ifDefined(item.item.icon)}" .associatedItem="${ifDefined(item.item)}" text="${ifDefined(item.item.text)}" ?has-children="${item.item.items.length}" ?expanded="${item.item.expanded}" ?selected="${item.selected}">${ !context.collapsed ? block8(item, index, context) : undefined }</${scopeTag("ui5-tree-item", tags, suffix)}>`; };
const block8 = (item, index, context) => { return html`${ repeat(item.item.items, (item, index) => item._id || index, (item, index) => block9(item, index, context)) }`; };
const block9 = (item, index, context) => { return html`<${scopeTag("ui5-tree-item", tags, suffix)} .associatedItem="${ifDefined(item)}" text="${ifDefined(item.text)}" ?selected="${item.selected}"></${scopeTag("ui5-tree-item", tags, suffix)}>`; };


	return block0(context);
};

export default main;