
/* eslint no-unused-vars: 0 */
import { ifDefined } from "lit-html/directives/if-defined.js";
import { html, svg, repeat, classMap, styleMap, unsafeHTML, scopeTag } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';

const main = (context, tags, suffix) => {
	const block0 = (context) => { return html`<div class="ui5-wiz-root" aria-label="${ifDefined(context.ariaLabelText)}" role="region"><nav class="ui5-wiz-nav" aria-label="${ifDefined(context.navAriaLabelText)}" tabindex="-1"><div class="ui5-wiz-nav-list" role="list" aria-label="${ifDefined(context.listAriaLabelText)}" aria-describedby="wiz-nav-descr" aria-controls="${ifDefined(context._id)}-wiz-content">${ repeat(context._stepsInHeader, (item, index) => item._id || index, (item, index) => block1(item, index, context)) }</div></nav><span id="wiz-nav-descr" class="ui5-hidden-text">${ifDefined(context.navAriaDescribedbyText)}</span><div id="${ifDefined(context._id)}-wiz-content" class="ui5-wiz-content" @scroll="${context.onScroll}">${ repeat(context._steps, (item, index) => item._id || index, (item, index) => block2(item, index, context)) }</div></div>`; };
const block1 = (item, index, context) => { return html`<${scopeTag("ui5-wizard-tab", tags, suffix)} title-text="${ifDefined(item.titleText)}" subtitle-text="${ifDefined(item.subtitleText)}" icon="${ifDefined(item.icon)}" number="${ifDefined(item.number)}" ?disabled="${item.disabled}" ?selected="${item.selected}" ?hide-separator="${item.hideSeparator}" ?active-separator="${item.activeSeparator}" ?branching-separator="${item.branchingSeparator}" ._wizardTabAccInfo="${ifDefined(item.accInfo)}" data-ui5-content-ref-id="${ifDefined(item.refStepId)}" data-ui5-index="${ifDefined(item.pos)}" _tab-index="${ifDefined(item.tabIndex)}" @ui5-selection-change-requested="${ifDefined(context.onSelectionChangeRequested)}" @ui5-focused="${ifDefined(context.onStepInHeaderFocused)}" @click="${context._onGroupedTabClick}" style=${ifDefined(item.styles)}></${scopeTag("ui5-wizard-tab", tags, suffix)}>`; };
const block2 = (item, index, context) => { return html`<div class="ui5-wiz-content-item" ?hidden="${item.disabled}" ?selected="${item.selected}" ?stretch="${item.stretch}" data-ui5-content-item-ref-id="${ifDefined(item._id)}"><slot name="${ifDefined(item._individualSlot)}"></slot></div>`; };


	return block0(context);
};

export default main;