
/* eslint no-unused-vars: 0 */
import { ifDefined } from "lit-html/directives/if-defined.js";
import { html, svg, repeat, classMap, styleMap, unsafeHTML, scopeTag } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';

const main = (context, tags, suffix) => {
	const block0 = (context) => { return html`<div class="ui5-uc-root" role="region" aria-roledescription="${ifDefined(context._roleDescription)}" @drop="${context._ondrop}"><div class="ui5-uc-header"><slot name="header"></slot></div><div class="${classMap(context.classes.content)}"><${scopeTag("ui5-list", tags, suffix)} mode="${ifDefined(context.mode)}" @ui5-selection-change="${ifDefined(context._onSelectionChange)}" @ui5-item-delete="${ifDefined(context._onItemDelete)}"><slot></slot></${scopeTag("ui5-list", tags, suffix)}>${ context._showNoData ? block1(context) : block2(context) }</div></div>`; };
const block1 = (context) => { return html`<div class="uc-no-files"><div class="icon-container"><${scopeTag("ui5-icon", tags, suffix)} name="document"></${scopeTag("ui5-icon", tags, suffix)}></div><${scopeTag("ui5-title", tags, suffix)} level="H2" wrapping-type="Normal">${ifDefined(context._noDataText)}</${scopeTag("ui5-title", tags, suffix)}><${scopeTag("ui5-label", tags, suffix)} class="subtitle" wrapping-type="Normal">${ifDefined(context._noDataDescription)}</${scopeTag("ui5-label", tags, suffix)}></div>`; };
const block2 = (context) => { return html`${ context._showDndOverlay ? block3(context) : undefined }`; };
const block3 = (context) => { return html`<div class="${classMap(context.classes.dndOverlay)}" @dragenter="${context._ondragenter}" @dragleave="${context._ondragleave}" @dragover="${context._ondragover}"><${scopeTag("ui5-icon", tags, suffix)} name="upload-to-cloud"></${scopeTag("ui5-icon", tags, suffix)}><span class="dnd-overlay-text">${ifDefined(context._dndOverlayText)}</span></div>`; };


	return block0(context);
};

export default main;