
/* eslint no-unused-vars: 0 */
import { ifDefined } from "lit-html/directives/if-defined.js";
import { html, svg, repeat, classMap, styleMap, unsafeHTML, scopeTag } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';

const main = (context, tags, suffix) => {
	const block0 = (context) => { return html`<${scopeTag("ui5-popover", tags, suffix)} class="ui5-shellbar-menu-popover" placement-type="Bottom" @ui5-before-open=${ifDefined(context._menuPopoverBeforeOpen)} @ui5-after-close=${ifDefined(context._menuPopoverAfterClose)}><${scopeTag("ui5-list", tags, suffix)} separators="None" mode="SingleSelect" @ui5-item-press=${ifDefined(context._menuItemPress)}>${ repeat(context._menuPopoverItems, (item, index) => item._id || index, (item, index) => block1(item, index, context)) }</${scopeTag("ui5-list", tags, suffix)}></${scopeTag("ui5-popover", tags, suffix)}><${scopeTag("ui5-popover", tags, suffix)} class="ui5-shellbar-overflow-popover" placement-type="Bottom" horizontal-align="${ifDefined(context.popoverHorizontalAlign)}" hide-arrow @ui5-before-open=${ifDefined(context._overflowPopoverBeforeOpen)} @ui5-after-close=${ifDefined(context._overflowPopoverAfterClose)}><${scopeTag("ui5-list", tags, suffix)} separators="None" @ui5-item-press="${ifDefined(context._actionList.itemPress)}">${ repeat(context._hiddenIcons, (item, index) => item._id || index, (item, index) => block2(item, index, context)) }</${scopeTag("ui5-list", tags, suffix)}></${scopeTag("ui5-popover", tags, suffix)}>`; };
const block1 = (item, index, context) => { return html`${ifDefined(item)}`; };
const block2 = (item, index, context) => { return html`<${scopeTag("ui5-li", tags, suffix)} data-ui5-external-action-item-id="${ifDefined(item.refItemid)}" icon="${ifDefined(item.icon)}" type="Active" @ui5-_press="${ifDefined(item.press)}">${ifDefined(item.text)}</${scopeTag("ui5-li", tags, suffix)}>`; };


	return block0(context);
};

export default main;