
/* eslint no-unused-vars: 0 */
import { ifDefined } from "lit-html/directives/if-defined.js";
import { html, svg, repeat, classMap, styleMap, unsafeHTML, scopeTag } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';

const main = (context, tags, suffix) => {
	const block0 = (context) => { return html`<${scopeTag("ui5-responsive-popover", tags, suffix)} horizontal-align="Center" placement-type="Bottom" aria-label="${ifDefined(context.actionSheetStepsText)}" class="${classMap(context.classes.popover)}" @ui5-after-close=${ifDefined(context._afterClosePopover)} content-only-on-desktop prevent-focus-restore with-padding _hide-header><ul class="ui5-wizard-responsive-popover-list">${ repeat(context._groupedTabs, (item, index) => item._id || index, (item, index) => block1(item, index, context)) }</ul><div slot="footer" class="ui5-responsive-popover-footer"><${scopeTag("ui5-button", tags, suffix)} design="Transparent" @click="${context._closeRespPopover}">Cancel</${scopeTag("ui5-button", tags, suffix)}></div></${scopeTag("ui5-responsive-popover", tags, suffix)}>`; };
const block1 = (item, index, context) => { return html`<li><${scopeTag("ui5-button", tags, suffix)} icon="${ifDefined(item.icon)}" ?disabled="${item.disabled}" design="Transparent" data-ui5-header-tab-ref-id="${ifDefined(item.pos)}" @click="${context._onOverflowStepButtonClick}">${ifDefined(item.heading)}</${scopeTag("ui5-button", tags, suffix)}></li>`; };


	return block0(context);
};

export default main;