
/* eslint no-unused-vars: 0 */
import { ifDefined } from "lit-html/directives/if-defined.js";
import { html, svg, repeat, classMap, styleMap, unsafeHTML, scopeTag } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';

const main = (context, tags, suffix) => {
	const block0 = (context) => { return html`<li class="ui5-nli-group-root ui5-nli-focusable" @focusin="${context._onfocusin}" @focusout="${context._onfocusout}" @keydown="${context._onkeydown}" role="listitem" tabindex="${ifDefined(context._tabIndex)}" dir="${ifDefined(context.effectiveDir)}" aria-expanded="${ifDefined(context.ariaExpanded)}" aria-labelledby="${ifDefined(context.ariaLabelledBy)}" style="list-style-type: none;"><div class="ui5-nli-group-header"><${scopeTag("ui5-button", tags, suffix)} icon="navigation-right-arrow" design="Transparent" @click="${context._onBtnToggleClick}" class="ui5-nli-group-toggle-btn" title="${ifDefined(context.toggleBtnAccessibleName)}" aria-label="${ifDefined(context.toggleBtnAccessibleName)}"></${scopeTag("ui5-button", tags, suffix)}>${ context.hasPriority ? block1(context) : undefined }<div id="${ifDefined(context._id)}-heading" class="ui5-nli-group-heading" part="heading">${ifDefined(context.heading)}</div>${ context.showCounter ? block2(context) : undefined }<div class="ui5-nli-group-divider"></div>${ !context.collapsed ? block3(context) : undefined }${ context.showClose ? block7(context) : undefined }<span id="${ifDefined(context._id)}-invisibleText" class="ui5-hidden-text">${ifDefined(context.accInvisibleText)}</span></div><${scopeTag("ui5-list", tags, suffix)} class="ui5-nli-group-items" accessible-role="list"><slot></slot></${scopeTag("ui5-list", tags, suffix)}>${ context.busy ? block8(context) : undefined }</li>`; };
const block1 = (context) => { return html`<${scopeTag("ui5-icon", tags, suffix)} class="ui5-prio-icon ui5-prio-icon--${ifDefined(context.priorityIcon)}" name="${ifDefined(context.priorityIcon)}"></${scopeTag("ui5-icon", tags, suffix)}>`; };
const block2 = (context) => { return html`<span class="ui5-nli-group-counter">(${ifDefined(context.itemsCount)})</span>`; };
const block3 = (context) => { return html`${ context.showOverflow ? block4(context) : block5(context) }`; };
const block4 = (context) => { return html`<${scopeTag("ui5-button", tags, suffix)} icon="overflow" design="Transparent" @click="${context._onBtnOverflowClick}" class="ui5-nli-overflow-btn" title="${ifDefined(context.overflowBtnAccessibleName)}" aria-label="${ifDefined(context.overflowBtnAccessibleName)}"></${scopeTag("ui5-button", tags, suffix)}>`; };
const block5 = (context) => { return html`${ repeat(context.standardActions, (item, index) => item._id || index, (item, index) => block6(item, index, context)) }`; };
const block6 = (item, index, context) => { return html`<${scopeTag("ui5-button", tags, suffix)} icon="${ifDefined(item.icon)}" class="ui5-nli-action" ?disabled="${item.disabled}" design="${ifDefined(item.design)}" @click="${item.press}" data-ui5-external-action-item-id="${ifDefined(item.refItemid)}">${ifDefined(item.text)}</${scopeTag("ui5-button", tags, suffix)}>`; };
const block7 = (context) => { return html`<${scopeTag("ui5-button", tags, suffix)} icon="decline" design="Transparent" @click="${context._onBtnCloseClick}" title="${ifDefined(context.closeBtnAccessibleName)}" aria-label="${ifDefined(context.closeBtnAccessibleName)}" close-btn></${scopeTag("ui5-button", tags, suffix)}>`; };
const block8 = (context) => { return html`<${scopeTag("ui5-busy-indicator", tags, suffix)} delay="${ifDefined(context.busyDelay)}" active size="Medium" class="ui5-nli-busy"></${scopeTag("ui5-busy-indicator", tags, suffix)}>`; };


	return block0(context);
};

export default main;